// @flow
/* eslint-disable no-console */

export class AppException extends Error {
  code: string

  constructor(message: string, causedBy?: Error) {
    super(message)
    this.name = "ApplicationError"

    //Error.captureStackTrace(this, AppException);
    if (typeof causedBy !== "undefined") {
      if (causedBy instanceof Error || (causedBy !== null && typeof causedBy === "object" && "stack" in causedBy)) {
        this.stack = this.stack + `\nCaused By:\n` + causedBy.stack

        // Propogate this up some info about the exception. Otherwise some test fail.
        this.code = causedBy.code
        this.message += ":" + causedBy.message
      } else {
        // see https://stackoverflow.com/questions/26020578/should-a-promise-reject-message-be-wrapped-in-error
        this.stack = this.stack + `\nCaused By (this should have been an error obj): ` + causedBy
      }
    }
  }
}

export default AppException
