//@flow
import { DateTime } from "luxon"
import { parseQueryString, stringifyQueryString } from "./oauth"
import type { LimioObject, Offer, Schedule } from "@i42/shared/src/objects/types"
import * as R from "ramda"

export function validateEmail(email: string, validEmailRegex: string = ""): boolean {
  let validEmail
  if (validEmailRegex) {
    validEmail = new RegExp(validEmailRegex)
  } else {
    validEmail = /^\S+@\S+\.\S+$/
  }

  return validEmail.test(email)
}

export function validatePhoneNumber(phoneNumber: string, validPhoneRegex: string = ""): boolean {
  let validPhone = new RegExp(validPhoneRegex)

  return validPhone.test(phoneNumber)
}

type LUXON_DATE_FORMATS = "DATE_SHORT" | "DATE_FULL" | "DATE_MED"
export type DateFormat = LUXON_DATE_FORMATS | "DATE_EN"

export function formatDate(date: string | number = Date.now(), format: DateFormat = "DATE_SHORT"): string {
  let formattedDate

  if (format === "DATE_EN") {
    formattedDate = DateTime.fromJSDate(new Date(date)).toFormat("d LLL yyyy")
  } else {
    formattedDate = DateTime.fromJSDate(new Date(date)).toLocaleString(DateTime[format])
  }

  return formattedDate
}

export function setDomainCookie(name: string, value: string) {
  const currentURL = new URL(window.location)
  const hostName = currentURL.hostname.substring(currentURL.hostname.lastIndexOf(".", currentURL.hostname.lastIndexOf(".") - 1) + 1)

  document.cookie = `${name}=${encodeURIComponent(value)}; domain=${hostName}; path=/; max-age=${60 * 60}` // an hour should be fine for now
}

export function clearCookie(name: string) {
  document.cookie = `${name}=;Max-Age=0`
}

export function setTrackingTags(offer: LimioObject<Offer>) {
  const url = new URL(window.location)
  url.searchParams.set("lmo_offer", offer.path)
  const attributes = offer.data.attributes || {}
  const attributeString = stringifyQueryString(attributes)

  const trackingTags = ["utm", "ltm"]
  for (const [key, value] of Object.entries(getTrackingTags(attributeString, trackingTags))) {
    url.searchParams.set(key, value)
  }
  window.history.replaceState({}, "", url)
}

export function getTrackingTags(searchString: string, prefixes: string[]) {
  const tags = {}

  const checkPrefix = (s: string) => prefixes.filter(p => s.startsWith(p))
  const queryParamPairs = Object.entries(parseQueryString(searchString))
  const filteredPairs = queryParamPairs.filter(([key, value]) => checkPrefix(key)[0])
  return Object.fromEntries(filteredPairs)
}

export function getPaymentAmount(
  status: string,
  hasRecurringCharge: boolean,
  nextSchedule: LimioObject<Schedule>,
  offer: LimioObject<Offer>,
  currencies: Object
): string {
  if (status === "cancelled") {
    return "N/A"
  }

  let paymentAmount
  // sometimes a zuora preview or zuora renewal fail may result in a sub to not have a price available
  // if this happens we don't want to render undefined.

  // check schedule first renewals and external pricing use this
  if (nextSchedule?.data?.amount) {
    paymentAmount = ` ${R.path([nextSchedule.data.currency, "symbol"], currencies)}${nextSchedule.data.amount}`

    // check price__limio for one off offers
  } else if (!hasRecurringCharge && offer?.data?.attributes?.price__limio?.[0]?.value) {
    paymentAmount = ` ${R.path([offer.data.attributes.price__limio?.[0].currencyCode, "symbol"], currencies)}${offer.data.attributes.price__limio[0].value}`
  } else {
    paymentAmount = "Currently unavailable"
  }
  return paymentAmount
}

export function getRenewalDate(status: string, nextSchedule: LimioObject<Schedule>, subData: Object, dateFormat: DateFormat): string {
  if (status === "cancelled") {
    return "N/A"
  }

  return formatDate(nextSchedule?.data?.date || subData.data.termEndDate, dateFormat)
}

export function getEndOfTermMessage(status: string, subData: Object, dateFormat: DateFormat): string {
  if (status === "cancelled" && subData.data.termEndDate > new Date().toISOString()) {
    return `Your subscription will end on ${formatDate(subData.data.termEndDate, dateFormat)}`
  } else {
    return ""
  }
}
