//@flow
import { getCookie } from "@i42/shared/src/helpers"
import * as R from "ramda"
import { DateTime } from "luxon"
import type { LimioObject, Offer } from "@i42/shared/src/objects/types"

export const checkHasDelivery = orderItems => {
  let result = false

  orderItems.forEach(item => {
    if (item.data.products) {
      item.data.products.forEach(product => {
        if (R.path(["attributes", "has_delivery__limio"], product)) {
          result = true
        }
      })
    }
  })

  return result
}

export const checkIsGift = orderItems => (orderItems.length ? orderItems.map(item => !!item?.data?.attributes?.is_gift__limio).includes(true) : false)

export const checkIsRedeem = orderItems => (orderItems.length ? orderItems.map(item => !!item?.data?.attributes?.is_redeem_gift__limio).includes(true) : false)

export const checkAllowedCountries = orderItems => {
  if (orderItems.length) {
    const allAllowedCountries = orderItems.map(({ data }) => data?.attributes?.allowed_countries__limio || [])
    const allowedCountries = R.uniq(R.flatten(allAllowedCountries))

    return allowedCountries
  }

  return []
}

export const checkLocale = (orderItems, state) => {
  if (orderItems.length) {
    // we assume all order items have the same locales, so let's just pick the first one
    const allLocales = orderItems.map(({ data }) => data?.attributes?.locale__limio || [])
    const [locale] = R.uniq(R.flatten(allLocales))

    const mode = R.path(["appConfig", "mode"], state)
    const defaultLocale = R.path(["appConfig", mode, "shop", "default_locale"], state)

    return locale || defaultLocale
  }

  return []
}

export const checkCountry = (orderItems, billingCountry, providedCountries) => {
  const allowedCountries = providedCountries || checkAllowedCountries(orderItems)
  let limioCountry = getCookie("limio-country")

  const searchParams = new URL(window.location).searchParams
  if (searchParams.get("lmo_co")) {
    limioCountry = searchParams.get("lmo_co")
  }

  if (!allowedCountries.length && (billingCountry || limioCountry)) {
    return billingCountry || limioCountry
  }

  const [defaultCountry] = allowedCountries

  const country = allowedCountries.includes(limioCountry) ? limioCountry : defaultCountry

  if (billingCountry) {
    return allowedCountries.includes(billingCountry) ? billingCountry : country
  }

  return country
}

export const checkRequestCompany = orderItems =>
  orderItems.length ? orderItems.map(item => !!item?.data?.attributes?.request_company_info__limio).includes(true) : false

export const checkChooseDate = orderItems =>
  orderItems.length ? orderItems.map(item => !!item?.data?.attributes?.choose_gift_date__limio).includes(true) : false

export const checkStudent = orderItems => (orderItems.length ? orderItems.map(item => !!item?.data?.attributes?.student__limio).includes(true) : false)

export const checkTrial = orderItems => (orderItems.length ? orderItems.map(item => !!item?.data?.attributes?.trial__limio).includes(true) : false)

export const checkAutoRenew = orderItems => (orderItems.length ? orderItems.map(item => !!item?.data?.attributes?.autoRenew__limio).includes(true) : false)

export const checkCurrentSchedule = (schedules: [] = [], allowCancelled?: boolean) => {
  const activeSchedules = allowCancelled ? schedules : schedules.filter(x => ["active", "pending", "pending-external"].includes(x.status))
  const currentDate = DateTime.utc().toISO()

  const sortedSchedules = activeSchedules.sort((a, b) => new Date(a.data.date) - new Date(b.data.date))
  let nextSchedule = sortedSchedules.find(schedule => schedule.data.date > currentDate && ["active", "pending", "pending-external"].includes(schedule.status))

  if (!nextSchedule && allowCancelled) {
    nextSchedule = sortedSchedules.find(schedule => schedule.data.date > currentDate)
  }

  return nextSchedule
}

export const checkPreviousSchedule = (schedules: [] = []) => {
  const activeSchedules = schedules.filter(schedule => ["active", "pending", "pending-external"].includes(schedule.status))
  const currentDate = DateTime.utc().toISO()

  const prevSchedules = activeSchedules.filter(schedule => schedule.data.date < currentDate)
  const sortedSchedules = prevSchedules.sort((a, b) => new Date(b.data.date) - new Date(a.data.date))

  return sortedSchedules?.[0]
}

export const checkPayment = (schedules: [] = []) => {
  const activeSchedules = schedules.filter(schedule => ["active", "pending", "pending-external"].includes(schedule.status))
  return activeSchedules.find(schedule => DateTime.fromISO(schedule.data.date).day === DateTime.fromISO(new Date().toISOString()).day)
}

export function checkActiveOffers(offers: LimioObject<Offer>[] = [], includeFuture: boolean = false): LimioObject<Offer>[] {
  const sortedOffers = offers.sort((a, b) => new Date(a.data.start) - new Date(b.data.start))
  const currentDate = DateTime.utc().toISO()
  // currentDate takes in the current date and time i.e. 2021-12-15T22:42:08.588Z
  let currentActiveOffers = sortedOffers.filter(relatedOffer => !relatedOffer.data?.end || DateTime.fromISO(relatedOffer.data?.end).toString() >= currentDate)
  //The end date is currently in the format 2021-12-15
  //Its a current active offer if there is no end date in the offer data or the currentDate is > or = to the end date (2021-12-15T00:00:00.000+00:00)

  if (!includeFuture) {
    currentActiveOffers = currentActiveOffers.filter(relatedOffer => relatedOffer.data?.start <= currentDate) // Offer might be future dated for next term etc.
  }

  return currentActiveOffers
}
