// @flow
// copied from https://github.com/aws-amplify/amplify-js/blob/master/packages/auth/src/OAuth/OAuth.ts
// broswer friendly helpers for oauth
import sha256 from "crypto-js/sha256"
import Base64 from "crypto-js/enc-base64"

export function generateState(length: number) {
  let result = ""
  let i = length
  const chars = "0123456789abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ"
  for (; i > 0; --i) result += chars[Math.round(Math.random() * (chars.length - 1))]
  return result
}

export function generateChallenge(code: string) {
  return base64URL(sha256(code))
}

export function base64URL(string) {
  return string.toString(Base64).replace(/=/g, "").replace(/\+/g, "-").replace(/\//g, "_")
}

export function generateRandom(size: number): string {
  const CHARSET = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789-._~"
  const buffer = new Uint8Array(size)
  if (typeof window !== "undefined" && !!window.crypto) {
    window.crypto.getRandomValues(buffer)
  } else {
    for (let i = 0; i < size; i += 1) {
      buffer[i] = (Math.random() * CHARSET.length) | 0
    }
  }
  return bufferToString(buffer)
}

function bufferToString(buffer: Uint8Array) {
  const CHARSET = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789"
  const state = []
  for (let i = 0; i < buffer.byteLength; i += 1) {
    const index = buffer[i] % CHARSET.length
    state.push(CHARSET[index])
  }
  return state.join("")
}

export function parseQueryString(str: ?string): { [string]: string } {
  if (!str) {
    return {}
  }

  const startPos = str.indexOf("?")
  if (startPos !== -1) {
    str = str.slice(startPos + 1)
  }

  return str
    .split("&")
    .map(pairings => pairings.split("="))
    .reduce((accum, [k, v]) => ({ ...accum, [k]: typeof v === "undefined" ? undefined : decodeURIComponent(v) }), {})
}

export function stringifyQueryString(params: { [string]: any }): string {
  return Object.entries(params)
    .map(([key, value]) => {
      if (typeof value === "undefined") {
        return undefined
      }

      let str = encodeURIComponent(key)
      if (value) {
        if (typeof value === "string") {
          str += `=${encodeURIComponent(value)}`
        } else if (typeof value?.toString === "function") {
          str += `=${encodeURIComponent(value.toString())}`
        }
      }
      return str
    })
    .filter(str => typeof str !== "undefined")
    .join("&")
}
